.upcoming-schedule {
    background-color: var(--just-white);
    border: 1px solid;
    border-color: #e0e0e0;
    border-radius: 12px;
    height: 480px;
    overflow: hidden;
    position: relative;
    width: 427px;
}

.upcoming-schedule .text-wrapper {
    color: var(--primary-subtitle-color);
    font-family: "Poppins-Regular", Helvetica;
    font-size: 12px;
    font-weight: 400;
    height: 23px;
    left: 18px;
    letter-spacing: 0;
    line-height: 28px;
    position: absolute;
    top: 63px;
    white-space: nowrap;
    width: 41px;
}

.upcoming-schedule .text-wrapper-2 {
    color: var(--primary-subtitle-color);
    font-family: "Poppins-Regular", Helvetica;
    font-size: 12px;
    font-weight: 400;
    height: 23px;
    left: 18px;
    letter-spacing: 0;
    line-height: 28px;
    position: absolute;
    top: 167px;
    white-space: nowrap;
    width: 41px;
}

.upcoming-schedule .frame-2 {
    align-items: center;
    display: flex;
    justify-content: space-between;
    left: 20px;
    position: absolute;
    top: 20px;
    font-size: 17px;
    width: 386px;
    font-weight: 600;
    font-family: "Poppins";
}


.upcoming-schedule .frame-3 {
    align-items: center;
    background-color: #ffffff;
