    display: flex;
    justify-content: space-between;
    padding: 0px 6px;
    position: relative;
    width: 145px;
}

.upcoming-schedule .div-wrapper {
    align-items: flex-start;
    display: flex;
    gap: 10px;
    height: 27px;
    padding: 2px;
    position: relative;
    width: 109px;
}

.upcoming-schedule .text-wrapper-4 {
    color: var(--secondary-subtitle-color);
