.frame {
    align-items: center;
    background-color: #ffffff;
    border: 0.5px solid;
    border-color: #e3e1e1;
    border-radius: 6px;
    display: flex;
    gap: 29px;
    overflow: hidden;
    padding: 10px 16px;
    position: relative;
   
}

.div {
    align-items: flex-start;
    display: flex;
    flex: 1;
    flex-direction: column;
    flex-grow: 1;
    gap: 6px;
    position: relative;
}

.this-is-title {
    color: #000000;
    font-family: "Poppins-Medium", Helvetica;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 24px;
    margin-right: -25.00px;
    margin-top: -1.00px;
    position: relative;
    width: 283px;
}

.text-wrapper {
    align-self: stretch;
    color: var(--primary-subtitle-color);
    font-family: "Roboto-Regular", Helvetica;
    font-size: 10px;
    font-weight: 400;
    letter-spacing: 0;
    line-height: 14px;
    position: relative;
}

.img {
    height: 24px;
    position: relative;
    width: 68px;
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');
